<template>
	<view>
		<div :class="style">
			<div class="ta_c">
				<img class="succimg_cls" :src='img' >
			</div>
			<div class="pre_line status_tips ta_c">{{content}}</div>
			<div class='ta_c'><button class="button_cls flex_c" @click='do_fn(btn_fn)'>{{btn_txt}}</button></div>
		</div>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
				title: '状态提示页',// 状态提示页
				style: 'pink_style',// pink_style red_style skyblue_style
				img: 'http://webh5.wangjiangwei.top/01-project/03-hzbixin/09-zxyp/01-wx_public_h5/code/img/ok@2x.png',// succimg_cls 
				content: '提交成功，请等待后台审核',
					
				btn_txt: '确定',// 确定 完成
				btn_fn: 'back',// go_home

				// 未实现 ----------------
				status_obj: {
					success: {
						img: '',
						content: '',
					},
				},
				status: 'success',
				img_type: 'img',
			}
		},

		// 页面首次加载 
		onLoad(options) {
			console.log('onLoad 页面加载', options);
			options.contents&&(options.content=options.contents);
			this.setData(options);

			// options.title
			this.title
			&&
			wx.setNavigationBarTitle({
				title: this.title,
			});
			
		},
		
		methods: {
	
			do_fn: function (fn_name) {
				console.log('执行动态事件', arguments);
				fn_name&&this[fn_name]&&this[fn_name]();
			},
			back: function () {
				console.log('返回上一页', arguments);
				wx.navigateBack();
			},
			go_home: function () {
				console.log('前往首页', arguments);
				this.jump(this.create_dataset({
					url: '/pages/index/index',
					type: 3,
				}))
			},
		}
	}
</script>


